import { Notification } from "element-ui";

let notificationInstances = [];

const resetNotification = (options) => {
  // 关闭所有之前的实例
  console.log(notificationInstances, "-------");

  notificationInstances.forEach((instance) => {
    instance.close();
  });
  // 清空数组
  notificationInstances = [];

  // 创建新的通知实例，并将其添加到数组中
  const instance = Notification(options);
  notificationInstances.push(instance);
  console.log(notificationInstances, "notificationInstances");
};

const typeArr = ["success", "error", "warning", "info"];
typeArr.forEach((type) => {
  resetNotification[type] = (options) => {
    if (typeof options === "string") options = { message: options };
    options.type = type;
    return resetNotification(options);
  };
});

export { resetNotification };
