<template>
    <div>
        <button @click="addNewEditor">新增一页</button>
        <button @click="addNewSection">新增一节</button>
        <div v-for="(editor, index) in editors" :key="index" v-show="currentPage === editor.page">
            <WangEditor ref="editorsRef" :key="index" :page="editor.page" />
        </div>
        <div>
            <button v-for="page in totalPages" :key="page" @click="goToPage(page)">页码 {{ page }}</button>
        </div>
        <button @click="generateImage">提交</button>
    </div>
</template>

<script>
import WangEditor from './editB.vue';

export default {
    components: {
        WangEditor
    },
    data() {
        return {
            editors: [{ page: 1 }, { page: 1 }],
            totalPages: 1,
            currentPage: 1,
            sections: [],
            editorsRefs: []
        };
    },
    mounted() {
        this.editorsRefs = this.$refs.editorsRef;
        this.editorsRefs.forEach((res, index) => {
            if (index == 1) {
                res.editor.disable()
            }

        })

    },
    methods: {

        addNewEditor() {

            this.editors.push({ page: this.currentPage });
            console.log(this.editors);
        },
        addNewSection() {
            this.totalPages++;
            this.currentPage = this.totalPages;
            this.editors.push({ page: this.currentPage });
        },
        goToPage(page) {
            this.currentPage = page;
        },
        generateImage() {
            console.log(this.$refs.editorsRef);
            let a = []
            a = this.$refs.editorsRef
            let b = a.map((editor, index) => {
                return {
                    id: index,
                    jie: index + 1,
                    ye: this.editors[index].page,
                    content: editor.getContent()
                };
            });
            console.log(b);


        },
    }
};
</script>