<template>
    <div>
        <el-row>
            <el-col :span="12">
                <el-input v-model="searchText" placeholder="搜索可选列" clearable></el-input>
                <el-checkbox-group v-model="availableColumnsSelection">
                    <el-checkbox v-for="(column, index) in availableColumns" :key="index" :label="column.prop"
                        :checked="column.checked" :disabled="column.disable" @change="aaa(column)">
                        {{ column.label }}
                    </el-checkbox>
                </el-checkbox-group>
            </el-col>
            <el-col :span="12">
                <el-input v-model="searchTextSelected" placeholder="搜索已选列" clearable></el-input>
                <div>
                    <div v-for="(column, index) in selectedColumnsSelection" :key="index" :label="column.prop"
                        :checked="column.checked" :disabled="column.disable" style="padding: 20px 0;">
                        <span> {{ column.label }}</span>
                        <span @click="upFnc(column, index)">上调</span>
                        <span @click="delFnc(column, index)" style="padding-left: 20px;">删除</span>
                    </div>
                </div>

            </el-col>
        </el-row>
        <el-button @click="quitFnc">回复默认</el-button>
        <el-button>取消</el-button>
        <el-button @click="submitFnc">确认</el-button>
    </div>
</template>

<script>
export default {
    props: {

        availableColumns: {
            type: Array,
            required: true,
            default: () => []
        },
        selectedColumns: {
            type: Array,
            required: true,
            default: () => []
        },
    },
    data() {
        return {
            dialogVisible: this.visible,
            searchText: '', // 可选列搜索文本
            searchTextSelected: '', // 已选列搜索文本,
            availableColumnsSelection: [],
            selectedColumnsSelection: [],
            type: ""
        };
    },
    watch: {
        visible(value) {
            console.log(value, "jkjkjk");
            this.dialogVisible = value;
        },
        selectedColumns(value) {
            this.selectedColumnsSelection = value;
        },
    },
    mounted() {
        // console.log(this.availableColumns);
        console.log(this.selectedColumns);
        this.availableColumns.forEach(i => {
            this.selectedColumns.forEach(y => {
                if (i.checked == true && i.prop == y.prop) {
                    console.log(y);
                    y.checked = true
                } else {
                    return
                }
            })

        })
    },
    methods: {
        upFnc(column, index) {
            // console.log();
            this.$emit('upFnc', column, index)
        },
        submitFnc() {

            this.$emit('submitFnc', this.selectedColumnsSelection, this.type)
        },
        availableColumnsFnc() {
            this.availableColumnsSelection = ['col2', 'col3', 'col4', 'col5', 'col6',]
        },
        quitFnc() {
            this.$emit('quitFnc')

        },
        delFnc(column, index) {
            console.log(column);
            this.availableColumnsSelection = this.availableColumnsSelection.filter(i => column.prop != i)
            this.selectedColumnsSelection.splice(index, 1)
            this.type = ""
            this.type = 'del'
        },
        onCancel() {
            this.$emit('updateColumns', this.selectedColumnsSelection);
        },
        onConfirm() {
            this.$emit('updateColumns', this.selectedColumnsSelection);
        },
        deleteSelectedColumns() {
            this.$emit('deleteSelectedColumns');
        },
        moveColumnUp() {
            this.$emit('moveColumnUp');
        },
        aaa(a) {
            console.log(a, this.selectedColumns);
            a.checked = !a.checked
            this.updateArray(a, this.selectedColumnsSelection);

            if (!a.checked) {
                this.type = ""
                this.type = 'del'
            } else {
                this.type = ""
                this.type = 'add'
            }
        },

        updateArray(obj, array) {
            // 检查对象是否已经存在于数组中
            const index = array.findIndex(item => item.prop === obj.prop);

            // 如果对象存在，删除它
            if (index !== -1) {
                array.splice(index, 1);
            } else {
                // 如果不存在，添加它
                array.push(obj);
            }
        }
    },
};
</script>