<!-- 字段筛选组件 -->
<template>
    <div class="k-field-filter" style="z-index: 99;">
        <div class="field-filter">

            <main class="transfer">
                <el-transfer filterable filter-placeholder="请输入关键词搜索" v-model="value" target-order="unshift"
                    :titles="['选项合集', '已选项合集']" :data="data" v-loading="loading" @right-check-change="choose">
                    <div slot-scope="{ option }">
                        <div class="default-tranfer-item" @mouseenter="indexKey = option.key"
                            @mouseleave="indexKey = null">
                            <span>{{ option.label }}</span>
                            <div v-show="value.includes(option.key) && indexKey === option.key" class="button-group">
                                <!--  阻止事件冒泡  -->
                                <!-- 自定义数据项，将上、下、底部、顶部的排序功能放在数据项里面 -->
                                <em class="el-icon-top"
                                    @click.stop.prevent="publicMobileMethod('handleUp', option.key)"></em>
                                <em class="el-icon-top"
                                    @click.stop.prevent="publicMobileMethod('handleTop', option.key)"></em>
                                <em class="el-icon-bottom"
                                    @click.stop.prevent="publicMobileMethod('handleDown', option.key)"></em>
                                <em class="el-icon-download"
                                    @click.stop.prevent="publicMobileMethod('handleBottom', option.key)"></em>
                            </div>
                        </div>
                    </div>
                </el-transfer>
            </main>
            <footer class="footer">
                <div></div>
                <div>
                    <el-button class="cancel" size="mini" @click="cancel">取消</el-button>
                    <el-button class="determine" size="mini" @click="determine">确定</el-button>
                </div>
            </footer>
        </div>
    </div>
</template>

<script>

export default {
    name: 'KFieldFilter',
    /**
     * 初始化数据
     * @returns {{item: *[], data: *[], indexKey: null, value: *[]}}
     */
    data() {
        return {
            data: [], // 全部数据
            value: [], // 选中数据
            item: [], // 右侧勾选数据
            indexKey: null, // 高亮显示
        }
    },
    props: {
        cities: {
            type: Array,
            /**
             * 全部数据
             * @returns {*[]}
             */
            default: () => [],
        },
        filterSelected: {
            type: Array,
            /**
             * 右侧数据
             * @returns {*[]}
             */
            default: () => [],
        },
        loading: {
            type: Boolean,
            default: false,
        },
    },
    methods: {
        /**
         * 初始数据集
         * @returns {*[]}
         */
        async generateData() {
            const data = []
            this.value = []
            await this.cities.forEach((city) => {
                data.push({
                    label: city.label,
                    key: city.key,
                })
            })
            this.data = data
            console.log(data, "data");

            this.value = JSON.parse(JSON.stringify(this.filterSelected))
        },
        /**
         * 关闭弹窗
         */
        closeFilter() {
            this.$emit('closeFilter', false)
        },
        /**
         * 点击x号
         */
        closeWindow() {
            this.cancel()
        },
        /**
         * 确定选择
         */
        determine() {
            this.$emit('determine', this.value)
            this.closeFilter()
        },
        /**
         * 取消选择
         */
        cancel() {
            this.closeFilter()
            this.generateData()
        },
        /**
         * 监听右侧选中
         */
        choose(value) {
            this.item = value
        },
        /**
         * 右侧数据点击排序
         */
        publicMobileMethod(direction, key) {
            const self = this
            let index
            // 判断是否超出一条
            const item = self.item
            if (item.length === 1 || item.length === 0) {
                self.value.forEach((val, indexs) => {
                    // 获取需移动数据的下标
                    if (val === key) {
                        index = indexs
                    }
                })
                if (index === 0 && direction !== 'handleBottom' && direction !== 'handleDown') {
                    return self.$message('没有上移的空间了')
                }
                if (index === self.value.length - 1 && direction !== 'handleUp' && direction !== 'handleTop') {
                    return self.$message('没有下移的空间了')
                }
                // 改变的数组
                const changeItem = self.value[index]
                // 根据下标在数组中删除该数据
                self.value.splice(index, 1)
                // 判断加入数组位置
                if (direction) {
                    // 置顶
                    direction === 'handleTop' && self.value.unshift(changeItem)
                    // 置底
                    direction === 'handleBottom' && self.value.push(changeItem)
                    // 上移
                    direction === 'handleUp' && self.value.splice(index - 1, 0, changeItem)
                    // 下移
                    direction === 'handleDown' && self.value.splice(index + 1, 0, changeItem)
                }
            } else {
                self.$message.error('只能选择一条数据进行上下移动')
            }
        },
    },
    watch: {
        cities: {
            deep: true,
            immediate: true,
            handler() {
                this.generateData()
            },
        },
    },
    mounted() {
        this.generateData()
    },
}
</script>
