<style lang="scss">
.field-filter {
    .determine.el-button--mini {
        background-color: #2A76CD;
        color: #FFFFFF;
    }

    .el-transfer__button.cancel,
    .el-button--mini.cancel {

        &:focus,
        &:hover {
            background: #FFFFFF;
            border-color: #2A76CD;
            color: #2A76CD;
        }
    }

    .el-transfer {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .el-transfer-panel__item {
            margin-right: 0;
        }

        .default-tranfer-item {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .button-group {
                em {
                    margin-right: 8px;
                }
            }
        }

        .el-input.el-input--small {
            .el-input__inner {
                border-radius: 4px;
            }
        }

        .el-transfer__buttons {
            padding: 0;
            margin: 0 17px;

            .el-transfer__button {
                display: block;
                margin: 0 0 5px 0;
                padding: 4px 8px;
            }

            .el-button--primary.el-transfer__button {
                display: flex;
                justify-content: center;
                align-items: center;
                background: #2A76CD;
                border-color: #2A76CD;
            }

            .el-button--primary.is-disabled {
                background-color: #a0cfff;
                border-color: #a0cfff;
            }
        }

        .el-checkbox__input.is-indeterminate {
            .el-checkbox__inner {
                background: #2A76CD;
                border-color: #2A76CD;
            }
        }

        .el-transfer-panel {
            width: 49%;
            border-radius: 0;
        }

        .el-transfer-panel__body {
            .el-checkbox__label {
                &:hover {
                    color: #2A76CD;
                }
            }
        }

        .el-transfer-panel__header {
            .el-checkbox {
                .el-checkbox__label {
                    font-size: 14px;

                    span {
                        left: 100px
                    }
                }
            }
        }

        .el-transfer-panel__footer {
            top: 0;
            left: 61%;
            background-color: transparent;
            display: flex;
            width: 30%;
            justify-content: right;
            border-color: transparent;

            .el-button--text {
                color: #B5B5B5;
                margin-left: 5px;

                .icon-huidaodingbu {
                    font-size: 16px;
                }

                em {
                    font-size: 14px;
                    font-weight: 600;
                }
            }
        }

        .el-transfer-panel:first-child {
            .el-transfer-panel__header {
                .el-checkbox {
                    .el-checkbox__label {
                        span {
                            left: 84px;
                        }
                    }
                }
            }
        }
    }
}
</style>