<script>
import HelloWorld from "@/components/HelloWorld.vue";
import TableTool from "@/components/TableTool.vue";
import ColumnDialog from "./ColumnDialog.vue";
import printJS from "print-js";

import RegionScreenshot from "region-screenshot-js";
import $ from "jquery";
import ScreenShort from "js-web-screen-shot";
import { message } from '@/utils/resetMessage.js'
import { resetNotification } from '@/utils/resetNotification.js'
export default {
  name: "HomeView",
  components: {
    HelloWorld,
    TableTool,
    ColumnDialog,

  },
  computed: {
    popoverStyle() {
      return {
        left: this.popoverPosition.left + "px",
        top: this.popoverPosition.top + 14 + "px",
      };
    },
  },
  data() {
    return {

      src: "",
      uploadlist: [],
      imageFilelist: [],
      arrowPosition: {
        left: "",
        top: "",
      },

      popoverPosition: { left: 0, top: 0 },
      form: {
        address: "https://www.baidu.com/", // 地址信息
        name: "张三",
        password: "123",
      },
      isShow: false,
      dialogVisible: false,
      checked: false,
      checked2: false,
      summaryShow: false,
      summary: [], // 用于存放总计数据的数组
      searchValue: "",
      CheckboxArr: [],
      ids: [], // 初始化ids为空数组
      type: "",
      currentSummaryColumn: null, // 当前点击的列名
      tableData: [
        {
          date: "2016-05-03",
          name: 56,
          address: 8,
          id: "0",
        },
        {
          date: "2016-05-02",
          name: 78,
          address: 8,
          id: "1",
        },
        {
          date: "2016-05-04",
          name: 0,
          address: 8,
          id: "2",
        },
      ],
      showFilterTool: false,
      CheckboxArr: [],
      ids: [],
      type: "",
      currentSummaryColumn: null, // 当前点击的列名
      timer: null
    };
  },
  mounted() {
    localStorage.setItem("CheckboxArr", JSON.stringify(this.tableData));
    this.closeTableToolFnc();
    //添加水印
    // console.log(this.$refs.pageContainer);
    // watermark.set('1234567', '谁在花里胡哨', this.$refs.pageContainer)
  },
  beforeDestroy() {
    this.closeTableToolFnc();
  },
  methods: {
    open() {
      message({
        message: '恭喜你，这是一条成功消息',
        type: 'success'
      });
      message({
        message: '恭喜你，这是一条成功消息',
        type: 'error'
      });

    },
    open2() {
      resetNotification({
        title: '成kjhhj功',
        message: '这是一条成功的提示消息',
        type: 'success',
      });
      resetNotification({
        title: '成功',
        message: '这是一条成功的提示消息',
        type: 'error'
      });
    },

    screenshotFnc() {
      this.timer = setTimeout(() => {
        new ScreenShort({
          hiddenScrollBar: {
            state: true,
            fillState: false,

          },
          // 配置截图工具那个不显示，text是文字功能不显示，
          hiddenToolIco: {
            text: false
          },
          enableWebRtc: false,  //是否显示选项框
          level: 9999, //层级级别
          completeCallback: this.callback,
          closeCallback: this.closeFn,
        });
      }, 100)


    },

    callback(base64data) {
      var image = new Image();
      console.log(base64data);
      this.src = base64data
      image.src = base64data;

      image.onload = () => {
        var canvas = this.convertImageToCanvas(image);
        var url = canvas.toDataURL("image/jpeg");
        // 在页面展示的数组，次数组是要把截图成功后的图片在页面上展示出来的数组，可以根据自己项目的需求进行添加
        this.uploadlist.push({ image: url })
        for (var i = 0; i < this.uploadlist.length; i++) {
          if (this.uploadlist.length >= 6) {
            this.uploadhide = false;
          }
        }
        console.log(this.uploadlist, 'push')

        var bytes = window.atob(url.split(",")[1]);  //通过atob将base64进行编码
        //处理异常，将ASCII码小于0的转换为大于0,进行二进制转换
        var buffer = new ArrayBuffer(bytes.length);
        var uint = new Uint8Array(buffer);  //生成一个8位数的数组
        for (var i = 0; i < bytes.length; i++) {
          uint[i] = bytes.charCodeAt(i);  //根据长度返回相对应的Unicode 编码
        }
        //Blob对象
        var imageFile = new Blob([buffer], { type: "image/jpeg" }); //type为图片的格式

        this.imageFilelist.push(imageFile);
        if (this.imageFilelist != "") {
          console.log("截图成功")
          this.$message({
            type: "success",
            message: "截图成功",
          });
        }
      };


    },

    convertImageToCanvas(image) {
      var canvas = document.createElement("canvas");
      canvas.willReadFrequently = true;
      canvas.width = image.width;
      canvas.height = image.height;
      canvas.getContext("2d").drawImage(image, 0, 0);
      return canvas;
    },
    closeFn() {
      console.log("uuiu");
    },


    // 判断是否是 IE 浏览器
    isIE() {
      if (window.ActiveXObject || "ActiveXObject" in window) {
        return true;
      } else {
        return false;
      }
    },
    share(url) {
      if (this.isIE()) {
        this.$copyText(this.form.password);
        // this.$refs.addressInput.select(); // 实现选中效果
        this.$message.success("复制成功!");
      } else {
        let obj = {
          地址: this.form.address,
          用户名: this.form.name,
          密码: this.form.password,
        };
        const objectString = JSON.stringify(obj);
        this.$copyText(objectString)
          .then((res) => {
            // this.$refs.addressInput.select(); // 实现选中效果
            this.$message.success("复制成功!");
          })
          .catch((err) => {
            this.$message.error("该浏览器不支持自动复制, 请手动复制");
          });
      }
    },
    printFnc(url, filetype) {
      let path = url;
      let type = "";
      const acceptedFileTypes = ["png", "jpeg", "jpg", "gif"];
      console.log(acceptedFileTypes.includes(filetype));
      if (acceptedFileTypes.includes(filetype)) {
        type = "image";
      } else {
        type = filetype;
      }

      printJS({
        printable: path,
        type: type,
        // showModal: true,//开启加载modal
        onErrorL: (err) => {
          return this.$message({
            type: "error",
            message: "打印失败，请检查打印机设置或尝试保存文件！",
          });
        },
      });
    },
    //子组件只用@click.stop防止事件冒泡，父组件单击任意位置关闭子组件
    closeTableToolFnc() {
      document.addEventListener("click", (e) => {
        this.closeTool();
      });
    },
    filterData(e, type) {
      this.type = type;
      this.showFilterTool = true;
      this.$nextTick(() => {
        const buttonRect = e.target.getBoundingClientRect();
        const popoverRect = this.$refs.popover.getBoundingClientRect();
        this.arrowPosition.left = buttonRect.left;
        this.arrowPosition.top = buttonRect.top;

        let leftPosition =
          buttonRect.left - (popoverRect.width - buttonRect.width) / 2;

        // 调整popover位置，防止超出可视范围
        if (leftPosition < 0) {
          leftPosition = 10;
        } else if (leftPosition + popoverRect.width > window.innerWidth) {
          leftPosition = window.innerWidth - popoverRect.width - 10;
        }

        this.popoverPosition = {
          left: leftPosition,
          top: buttonRect.bottom,
        };
        this.ids = [];
        this.tableData.forEach((t) => {
          this.ids.push(t.id);
        });
        this.showFilterTool = true;
      });
    },
    closeTool() {
      this.showFilterTool = false;
    },

    saveSeach(data) {
      this.tableData = [];
      this.tableData = data;
      this.closeTool();
    },
  },
};
</script>
