<template>
    <div ref="editorElem"></div>
</template>

<script>
import WangEditor from 'wangeditor';

export default {
    mounted() {
        this.editor = new WangEditor(this.$refs.editorElem);
        this.editor.create();
        this.editor.txt.html('<span>111</span>'); // 设置编辑器的内容
        console.log(this.editor, "joioii");
        // this.editor.disable()
    },
    methods: {
        getContent() {
            // 获取编辑器内容的HTML结构
            return this.editor.txt.html();
        },
        setDefaultContent(content) {
            // 设置编辑器的内容
            this.editor.txt.html(content);
        },
        setContentEditable(isEditable) {
            // 设置内容是否可编辑
            // this.editor.txt.setContentEditable(isEditable);s
        },

    },
    beforeDestroy() {
        this.editor.destroy();
    }
};
</script>