import Vue from "vue";
// import { get as getDictDetail } from "@/api/system/dictDetail";
// 模拟的 getDictDetail 函数
function getDictDetail(dictName) {
  return new Promise((resolve) => {
    // 假数据
    const fakeData = {
      licensetype: [
        { value: "type1", label: "许可证类型1" },
        { value: "type2", label: "许可证类型2" },
      ],
      approvalcontent: [
        { value: "content1", label: "审批内容1" },
        { value: "content2", label: "审批内容2" },
      ],
      // ... 其他字典的假数据
    };

    // 模拟异步操作，延迟返回数据
    setTimeout(() => {
      resolve({
        content: fakeData[dictName] || [],
      });
    }, 100);
  });
}

export default class Dict {
  constructor(dict) {
    this.dict = dict;
  }

  async init(names, completeCallback) {
    if (names === undefined || name === null) {
      throw new Error("need Dict names");
    }
    const ps = [];
    names.forEach((n) => {
      Vue.set(this.dict.dict, n, {});
      Vue.set(this.dict.label, n, {});
      Vue.set(this.dict, n, []);
      ps.push(
        getDictDetail(n).then((data) => {
          this.dict[n].splice(0, 0, ...data.content);
          data.content.forEach((d) => {
            Vue.set(this.dict.dict[n], d.value, d);
            Vue.set(this.dict.label[n], d.value, d.label);
          });
        })
      );
    });
    await Promise.all(ps);
    completeCallback();
  }
}
